.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 80px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.modal-outer {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 3;
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 100px 0;
}
.modal-inner {
  background: #fff;
  margin: 0 auto;
  padding: 100px 0;
  width: 300px;
  height: 140px;
}
.modal-input{

}
.card-border {
  -webkit-box-shadow: 0 0.125rem 0.125rem 0 rgba(0, 0, 0, 0.14), 0 0.0625rem 0.3125rem 0 rgba(0, 0, 0, 0.12), 0 0.1875rem 0.0625rem -0.125rem rgba(0, 0, 0, 0.2);
          box-shadow: 0 0.125rem 0.125rem 0 rgba(0, 0, 0, 0.14), 0 0.0625rem 0.3125rem 0 rgba(0, 0, 0, 0.12), 0 0.1875rem 0.0625rem -0.125rem rgba(0, 0, 0, 0.2);
  border: 0.0625rem solid #bdbdbd;
  border-radius: 4px;
}
.profile-row {
  width: 600px;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 2em auto;
  padding: 2em 2em;
  -ms-flex-direction: row;
      flex-direction: row;
  background: #e0e0e0;
}

.avatar-div {
  display: block; 
  -ms-flex-positive: 1; 
      flex-grow: 1;
}
.profile-details {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 1em;
  -ms-flex-positive: 4;
      flex-grow: 4;
}

.match-card {
  width: 600px;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 2em auto;
  padding: 2em 2em;
  -ms-flex-direction: row;
      flex-direction: row;
  background: #e0e0e0;
}
.match-detail {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-direction: column;
      flex-direction: column;
}



